import {IconButton, ListItem, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat} from "../../utils/currency_format";
import TokenListItem from "../../components/TokenListItem";
import Transactions from "../../components/Transactions";
import CurrencyExchangeIcon from "@mui/icons-material/CurrencyExchange";
import {useSelector} from "react-redux";
import Web3 from "web3";
import bbks_abi from "../../abis/bbk_abi.json";
import {useState} from "react";
import CoineusLoading from "../../components/CoineusLoading";
import useTokenList from "../../hooks/useTokenList";
import HoldBBKS from "../../components/HoldBBKS";

export default function ETH(){

    const {wallet} = useSelector(state => state.coineus);

    const tokenList = useTokenList();

    const [pending, setPending] = useState(false);

    const claimRewards = (contract_address) => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(bbks_abi, contract_address);

            contract.methods
                .claimRewards()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    setPending(false);
                });
        }
    }

    return(<>
        {
            wallet.address && <Paper style={{marginTop: 10}}>
                <ListItem>
                    <ListItemText
                        primary={CoineusUSDFormat(wallet.totals.eth)}
                        secondary="Total ETH Value"
                        primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
            </Paper>
        }

        {/*ETH*/}
        <TokenListItem
            contract_address="native"
            network="eth"
            name="ETH"
            native
            price={wallet.prices.eth?.native}
            wrap="0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2"
        />

        {/*SRG*/}
        <TokenListItem
            contract_address="0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"
            name="SRG"
            network="eth"
            price={wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]}
            secondaryAction={<Transactions network="eth"/>}
        />


        {/*BBKS*/}
        <TokenListItem
            contract_address="0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"
            name="BBKS"
            network="eth"
            price={wallet.prices.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]}
            rewardsSymbol="SRG"
            rewards={wallet.info.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.pending_rewards}
            rewardsRealised={wallet.info.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.total_realised}
            rewardsUSD={wallet.info.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.pending_rewards * wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]?.inUSD}
            rewardsClaim={<IconButton
                color="primary"
                size="small"
                onClick={()=>claimRewards("0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25")}
                disabled={wallet.chain !== 1}>
                <CurrencyExchangeIcon fontSize="small"/>
            </IconButton>}

        />

        <HoldBBKS>
            {
                tokenList.eth.map(token => {
                    return <TokenListItem
                        key={token.contract_address}
                        contract_address={token.contract_address}
                        name={token.symbol}
                        network="eth"
                        price={wallet.prices.eth?.[token.contract_address]}
                    />
                })
            }
        </HoldBBKS>


        <CoineusLoading open={pending} label="Transaction Pending"/>
    </>)
}