import {
    Avatar, BottomNavigation, BottomNavigationAction,
    Container,
    Drawer, Fab, Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    ListSubheader, Skeleton, Snackbar, Typography,
} from "@mui/material";
import {useEffect, useState} from "react";
import axios from "axios";
import fallback_img from "../images/fallback.png";
import {useSelector} from "react-redux";
import Web3 from "web3";
import abi from "../abis/bbk_abi.json";
import IERC20_ABI from "../abis/IERC20.json";
import {makeBatchRequest} from "../utils/promisify";
import {formatWeiToNumber} from "../utils/format";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {copyTextToClipboard} from "../utils";

import CloseIcon from '@mui/icons-material/Close';
import WebAssetIcon from '@mui/icons-material/WebAsset';
import TwitterIcon from '@mui/icons-material/Twitter';
import TelegramIcon from '@mui/icons-material/Telegram';

export default function TokenDetails(props) {

    const {rpc, srg} = useSelector(state => state.coineus);
    const {network, contract_address} = props;

    const [open, setOpen] = useState(false);
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [info, setInfo] = useState({});
    const [details, setDetails] = useState({});
    const [loading, setLoading] = useState(true);

    const getTokenInfo = () => {
        if(contract_address === 'native')return;
        const web3 = new Web3(rpc[network]);
        const contract = new web3.eth.Contract(abi, contract_address);
        const srg_contract = new web3.eth.Contract(IERC20_ABI, srg[network]);

        const calls = [
            srg_contract.methods.balanceOf(contract_address).call,
            contract.methods.name().call,
            contract.methods.symbol().call,
            contract.methods.decimals().call,
            contract.methods.DIVISOR().call,
            contract.methods.SHAREDIVISOR().call,
            contract.methods.burnTax().call,
            contract.methods.buyMul().call,
            contract.methods.calculatePrice().call,
            contract.methods.getCirculatingSupply().call,
            contract.methods.getLiquidity().call,

            contract.methods.liqConst().call,
            contract.methods.getMarketCap().call,
            contract.methods.marketingShare().call,
            contract.methods.marketingWallet().call,
            contract.methods.maxBag().call,
            contract.methods.maxTX().call,
            contract.methods.owner().call,
            contract.methods.rewardShare().call,
            contract.methods.sellMul().call,
            contract.methods.taxBalance().call,

            contract.methods.charityShare().call,
            contract.methods.charityWallet().call,

            contract.methods.treasuryShare().call,
            contract.methods.treasuryWallet().call,

            contract.methods.teamShare().call,
            contract.methods.teamWallet().call,
            contract.methods.totalSupply().call,
            contract.methods.totalTx().call,
            contract.methods.totalVolume().call,
            contract.methods.getSRGPrice().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            const [
                tvl,
                name,
                symbol,
                decimals,
                DIVISOR,
                SHAREDIVISOR,
                burnTax,
                buyMul,
                calculatePrice,
                getCirculatingSupply,
                getLiquidity,
                liqConstant,
                getMarketCap,
                marketingShare,
                marketingWallet,
                maxBag,
                maxTX,
                owner,
                rewardShare,
                sellMul,
                taxBalance,
                charityShare,
                charityWallet,
                treasuryShare,
                treasuryWallet,
                teamShare,
                teamWallet,
                totalSupply,
                totalTx,
                totalVolume,
                SRGPrice
            ] = resp;

            const buyTax = ((1 - (buyMul/DIVISOR)) * 100).toFixed(0);
            const sellTax = ((1 - (sellMul/DIVISOR)) * 100).toFixed(0);

            const d = {
                taxes: {
                    taxBalance,
                    marketingWallet,
                    teamWallet,
                    charityWallet,
                    treasuryWallet,
                    buy: {
                        rewards: buyTax * (rewardShare / SHAREDIVISOR),
                        team: buyTax * (teamShare / SHAREDIVISOR),
                        marketing: buyTax * (marketingShare / SHAREDIVISOR),
                        charity: buyTax * (charityShare / SHAREDIVISOR),
                        treasury: buyTax * (treasuryShare / SHAREDIVISOR),
                        burn: burnTax,
                        total: buyTax
                    },
                    sell: {
                        rewards: sellTax * (rewardShare / SHAREDIVISOR),
                        team: sellTax * (teamShare / SHAREDIVISOR),
                        marketing: sellTax * (marketingShare / SHAREDIVISOR),
                        charity: sellTax * (charityShare / SHAREDIVISOR),
                        treasury: sellTax * (treasuryShare / SHAREDIVISOR),
                        burn: burnTax,
                        total: sellTax
                    }

                },
                price_srg: formatWeiToNumber(calculatePrice),
                price_usd: formatWeiToNumber(calculatePrice) * formatWeiToNumber(SRGPrice, 27),
                supply: formatWeiToNumber(totalSupply, decimals),
                circulating: formatWeiToNumber(getCirculatingSupply, decimals),
                liquidity: formatWeiToNumber(getLiquidity, decimals),
                liqConstant,
                mcap: formatWeiToNumber(getMarketCap, (network === 'bnb' ? 54 : 42)),
                maxBag: formatWeiToNumber(maxBag, decimals),
                maxTX: formatWeiToNumber(maxTX, decimals),
                totalTx: totalTx / 1,
                totalVolume: formatWeiToNumber(totalVolume, (network === 'bnb' ? 36 : 24)),
                owner,
                name,
                symbol,
                decimals,
                tvl: formatWeiToNumber(tvl, decimals)
            }
            setDetails(d);
            setLoading(false);
        })
    }

    useEffect(() => {
        if (!open) return;

        axios.get(`https://coineus.app/assets/tokens/${network}/${contract_address}/info.json`)
            .then(resp => {
                setInfo(resp.data);
            })
            .catch(err => {
            })

        getTokenInfo();
    }, [open])

    return (<>
        <div onClick={() => {
            if(contract_address != 'native')setOpen(true)
        }}>{props.children}</div>

        <Drawer
            anchor="bottom"
            open={open}
            onClose={() => setOpen(false)}
        >
            {
                loading && <Container>
                    <Skeleton height={60}/>
                    <Skeleton height={60}/>
                    <Skeleton height={60}/>
                </Container>
            }
            {
                !loading && <Container maxWidth="xs" style={{padding: 0}}>
                    {
                        (network === 'bnb' || network === 'eth') && <Grid container>
                            <Grid item xs={12} style={{minHeight: 400}}>
                                <iframe height="100%" width="100%" id="geckoterminal-embed" title="GeckoTerminal Embed"
                                        src={`https://www.geckoterminal.com/${network === 'bnb' ? 'bsc' : 'eth'}/pools/${contract_address.toLowerCase()}?embed=1&info=0&swaps=0`}
                                        frameBorder="0" allow="clipboard-write" allowFullScreen />
                            </Grid>
                        </Grid>
                    }
                    <Fab size="small" color="primary" style={{position: 'fixed', bottom: 20, right: 20}}
                         onClick={() => setOpen(false)}><CloseIcon/></Fab>
                    <List dense>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src={`https://coineus.app/assets/tokens/${network}/${contract_address}/logo.png`}>
                                    <img src={fallback_img} alt="Not Found"/>
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={details.name}
                                secondary={details.symbol}
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(details.price_srg) + " SRG"}
                                secondary={CoineusUSDFormat(details.price_usd)}
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListItem>
                            <BottomNavigation showLabels style={{flex:1, backgroundColor: 'transparent'}}>
                                <BottomNavigationAction label="Website" icon={<WebAssetIcon />} disabled={!info.links?.website} style={{opacity: !info.links?.website ? .3 : 1}} onClick={()=>window.open(info.links?.website)}/>
                                <BottomNavigationAction label="Twitter" icon={<TwitterIcon />} disabled={!info.links?.twitter} style={{opacity: !info.links?.twitter ? .3 : 1}} onClick={()=>window.open(info.links?.twitter)} />
                                <BottomNavigationAction label="Telegram" icon={<TelegramIcon />} disabled={!info.links?.telegram} style={{opacity: !info.links?.telegram ? .3 : 1}} onClick={()=>window.open(info.links?.telegram)} />
                            </BottomNavigation>
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                onClick={() =>{copyTextToClipboard(contract_address);setSnackbarOpen(true)}}
                                primary={contract_address}
                                secondary="Contract Address"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(details.tvl) + " SRG"}
                                secondary="TVL"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(details.mcap)}
                                secondary="Market Cap"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(details.supply)}
                                secondary="Supply"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(details.totalVolume)}
                                secondary="Total Volume"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={details.totalTx}
                                secondary="Total TXs"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(details.maxBag)}
                                secondary="Max Wallet"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(details.maxTX)}
                                secondary="Max TX"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        <ListSubheader>TAXES</ListSubheader>
                        <ListItem>
                            <ListItemText
                                primary={details.taxes?.buy?.total + "%"}
                                secondary="Buy Tax"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={details.taxes?.sell?.total + "%"}
                                secondary="Sell Tax"
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                        </ListItem>
                        {
                            (details.taxes?.buy?.rewards > 0 || details.taxes?.sell?.rewards > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.rewards || 0) + "%"}
                                    secondary="Rewards"
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.rewards || 0) + "%"}
                                    secondary="Rewards"
                                />
                            </ListItem>
                        }

                        {
                            (details.taxes?.buy?.burn > 0 || details.taxes?.sell?.burn > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.burn || 0) + "%"}
                                    secondary="Burn"
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.burn || 0) + "%"}
                                    secondary="Burn"
                                />
                            </ListItem>
                        }

                        {
                            (details.taxes?.buy?.marketing > 0 || details.taxes?.sell?.marketing > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.marketing || 0) + "%"}
                                    secondary={info.labels?.marketingWallet || "Marketing"}
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.marketing || 0) + "%"}
                                    secondary={info.labels?.marketingWallet || "Marketing"}
                                />
                            </ListItem>
                        }

                        {
                            (details.taxes?.buy?.charity > 0 || details.taxes?.sell?.charity > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.charity || 0) + "%"}
                                    secondary={info.labels?.charityWallet || "Charity"}
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.charity || 0) + "%"}
                                    secondary={info.labels?.charityWallet || "Charity"}
                                />
                            </ListItem>
                        }

                        {
                            (details.taxes?.buy?.treasury > 0 || details.taxes?.sell?.treasury > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.treasury || 0) + "%"}
                                    secondary={info.labels?.treasuryWallet || "Treasury"}
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.treasury || 0) + "%"}
                                    secondary={info.labels?.treasuryWallet || "Treasury"}
                                />
                            </ListItem>
                        }

                        {
                            (details.taxes?.buy?.team > 0 || details.taxes?.sell?.team > 0) && <ListItem>
                                <ListItemText
                                    primary={(details.taxes?.buy?.team || 0) + "%"}
                                    secondary={info.labels?.teamWallet || "Team"}
                                />
                                <ListItemText
                                    primary={(details.taxes?.sell?.team || 0) + "%"}
                                    secondary={info.labels?.teamWallet || "Team"}
                                />
                            </ListItem>
                        }
                    </List>
                </Container>
            }
            <Snackbar
                open={snackbarOpen}
                autoHideDuration={6000}
                onClose={()=>setSnackbarOpen(false)}
                message="Copied"
            />
        </Drawer>
    </>)
}