import {IconButton, ListItem, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat} from "../../utils/currency_format";
import TokenListItem from "../../components/TokenListItem";
import SRGONE from "../../components/ONES/SRGONE";
import getUserBalances from "../../utils/get_balances";
import CurrencyExchangeIcon from "@mui/icons-material/CurrencyExchange";
import {useSelector} from "react-redux";
import Web3 from "web3";
import bbks_abi from "../../abis/bbk_abi.json";
import {useState} from "react";
import CoineusLoading from "../../components/CoineusLoading";
import useTokenList from "../../hooks/useTokenList";
import HoldBBKS from "../../components/HoldBBKS";

export default function ARB() {

    const {wallet} = useSelector(state => state.coineus);

    const tokenList = useTokenList();

    const [pending, setPending] = useState(false);
    const claimRewards = (contract_address) => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(bbks_abi, contract_address);

            contract.methods
                .claimRewards()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    setPending(false);
                });
        }
    }

    return (<>
        {
            wallet.address && <Paper style={{marginTop: 10}}>
                <ListItem>
                    <ListItemText
                        primary={CoineusUSDFormat(wallet.totals.arb)}
                        secondary="Total ARB Value"
                        primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
            </Paper>
        }

        {/*ARETH*/}
        <TokenListItem
            contract_address="native"
            network="arb"
            name="ETH"
            native
            price={wallet.prices.arb?.native}
        />

        {/*SRG*/}
        <TokenListItem
            contract_address="0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF"
            network="arb"
            name="SRG"
            price={wallet.prices.arb?.["0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF"]}
        />

        {/*SRGONE*/}
        <TokenListItem
            contract_address="0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239"
            name="SRGONE"
            network="arb"
            price={wallet.prices.arb?.["0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239"]}
            secondaryAction={<SRGONE network="arb" onSuccess={() => getUserBalances(wallet.address)}/>}
            srgDetails={wallet.info.arb?.["0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239"]}
            detailsLabel="SRGONE"
        />

        <HoldBBKS>
            {
                tokenList.arb.map(token => {
                    return <TokenListItem
                        key={token.contract_address}
                        contract_address={token.contract_address}
                        name={token.symbol}
                        network="arb"
                        price={wallet.prices.arb?.[token.contract_address]}
                        rewardsSymbol="SRG"
                        rewards={wallet.info.arb?.[token.contract_address]?.pending_rewards}
                        rewardsRealised={wallet.info.arb?.[token.contract_address]?.total_realised}
                        rewardsUSD={wallet.info.arb?.[token.contract_address]?.pending_rewards * wallet.prices.arb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]?.inUSD}
                        rewardsClaim={<IconButton
                            color="primary"
                            size="small"
                            onClick={() => claimRewards(token.contract_address)}
                            disabled={wallet.chain !== 42161}>
                            <CurrencyExchangeIcon fontSize="small"/>
                        </IconButton>}
                    />
                })
            }
        </HoldBBKS>

        <CoineusLoading open={pending} label="Transaction Pending"/>

    </>)
}