import {useSelector} from "react-redux";
import {
    Container,
    Grid,
    ListItem,
    ListItemText,
    Paper,
    Tabs,
    Tab
} from "@mui/material";


import {useState} from "react";

import {CoineusUSDFormat} from "../utils/currency_format";

import PriceScroll from "../components/PriceScroll";
import ARB from "./Chains/ARB";
import ETH from "./Chains/ETH";
import BNB from "./Chains/BNB";


export default function HomePage() {

    const {wallet} = useSelector(state => state.coineus);

    const [chainTab, setChainTab] = useState('eth');

    return (<Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <PriceScroll/>

                <Grid item xs={12}>

                    {
                        wallet.address && <Paper style={{marginTop: 10}}>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusUSDFormat(
                                        wallet.totals.bnb +
                                        wallet.totals.eth +
                                        wallet.totals.arb
                                    )}
                                    secondary="Total Wallet Value"
                                    primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </Paper>
                    }

                    <Tabs
                        variant="fullWidth"
                        value={chainTab}
                        onChange={(ev, val) => {
                            setChainTab(val)
                        }}
                    >
                        <Tab id="eth" value="eth" label="ETH"/>
                        <Tab id="bnb" value="bnb" label="BNB"/>
                        <Tab id="arb" value="arb" label="ARB"/>
                    </Tabs>
                    {chainTab === 'eth' && <ETH />}
                    {chainTab === 'bnb' && <BNB />}
                    {chainTab === 'arb' && <ARB />}
                </Grid>
            </Grid>

        </Container>
    );
}