import {IconButton, ListItem, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat} from "../../utils/currency_format";
import TokenListItem from "../../components/TokenListItem";
import Transactions from "../../components/Transactions";
import SRGONE from "../../components/ONES/SRGONE";
import getUserBalances from "../../utils/get_balances";
import CurrencyExchangeIcon from "@mui/icons-material/CurrencyExchange";
import {useSelector} from "react-redux";
import Web3 from "web3";
import bbks_abi from "../../abis/bbk_abi.json";
import {useState} from "react";
import CoineusLoading from "../../components/CoineusLoading";
import useTokenList from "../../hooks/useTokenList";
import HoldBBKS from "../../components/HoldBBKS";

export default function BNB() {

    const {wallet} = useSelector(state => state.coineus);

    const tokenList = useTokenList();

    const [pending, setPending] = useState(false);
    const claimRewards = (contract_address) => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(bbks_abi, contract_address);

            contract.methods
                .claimRewards()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    setPending(false);
                });
        }
    }
    return (<>
        {
            wallet.address && <Paper style={{marginTop: 10}}>
                <ListItem>
                    <ListItemText
                        primary={CoineusUSDFormat(wallet.totals.bnb)}
                        secondary="Total BNB Value"
                        primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
            </Paper>
        }

        {/*BNB*/}
        <TokenListItem
            contract_address="native"
            network="bnb"
            name="BNB"
            native
            price={wallet.prices.bnb?.native}
            wrap="0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c"
        />

        {/*SRG*/}
        <TokenListItem
            contract_address="0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"
            name="SRG"
            network="bnb"
            price={wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]}
            secondaryAction={<Transactions network="bnb"/>}
        />

        {/*SRGONE*/}
        <TokenListItem
            contract_address="0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772"
            name="SRGONE"
            network="bnb"
            price={wallet.prices.bnb?.["0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772"]}
            secondaryAction={<SRGONE network="bnb" onSuccess={() => getUserBalances(wallet.address)}/>}
            srgDetails={wallet.info.bnb?.["0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772"]}
            detailsLabel="SRGONE"
        />

        <HoldBBKS>
            {
                tokenList.bnb.map(token => {
                    return <TokenListItem
                        key={token.contract_address}
                        contract_address={token.contract_address}
                        name={token.symbol}
                        network="bnb"
                        price={wallet.prices.bnb?.[token.contract_address]}
                        rewardsSymbol="SRG"
                        rewards={wallet.info.bnb?.[token.contract_address]?.pending_rewards}
                        rewardsRealised={wallet.info.bnb?.[token.contract_address]?.total_realised}
                        rewardsUSD={wallet.info.bnb?.[token.contract_address]?.pending_rewards * wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]?.inUSD}
                        rewardsClaim={<IconButton
                            color="primary"
                            size="small"
                            onClick={() => claimRewards(token.contract_address)}
                            disabled={wallet.chain !== 56}>
                            <CurrencyExchangeIcon fontSize="small"/>
                        </IconButton>}
                    />
                })
            }
        </HoldBBKS>

        <CoineusLoading open={pending} label="Transaction Pending"/>

    </>)
}