import {useSelector} from "react-redux";
import {Button, Grid, Paper, Typography} from "@mui/material";
import {useEffect, useState} from "react";
import Web3 from "web3";
import BBKSBNB_ABI from "../abis/BBKSBNB.json";

const holdBBKSAmount = 100;

function checkHold(wallet, status) {
    return true;
}

export default function HoldBBKS(props) {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [status, setStatus] = useState({
        amountBBKS: 0,
        amountBNB: 0,
        purchased: false,
        received: false
    });

    const getWalletInfo = () => {
        if (!wallet.address) return;

        const web3 = new Web3(rpc.bnb);
        const contract = new web3.eth.Contract(BBKSBNB_ABI, "0xb2ebDe646732c42453bf2A32Ae5844D30627B35d");

        contract.methods.getWalletInfo(wallet.address).call((err,resp) => {
            if(err){
                console.log("HomePage", err)
                return;
            }
            setStatus(resp)
        })
    }

    useEffect(getWalletInfo, [wallet.address, rpc.bnb]);

    if (!checkHold(wallet, status)) {
        return <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #723DF4', textAlign: 'center'}}>
                <Typography style={{marginBottom: 10}}>To unlock other SRG20 Tokens,<br/> you need to
                    hold <strong>{holdBBKSAmount} BBKS</strong></Typography>
                <Button variant="contained" href="https://swap.surgeprotocol.io" target="_blank">Buy BBKS</Button>
            </Paper>
        </Grid>
    }

    return <>{props.children}</>

}